/* OU Preloder */
#preloader {
  position: absolute;
  width: 100%;
  height: 100vh;
  margin: auto;
  opacity: 1;
  visibility: visible;
  flex-direction: column;
}
#preloader ~ body {
  overflow: hidden;
}
.ceHfpt {
  z-index: 9999;
  position: fixed;
  top: 0px;
  left: 0px;
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s linear 0s, visibility 0.2s linear 0.3s;
  height: 100vh;
  width: 100vw;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  display: flex;
  background: #f8f7f2;
}
.gNkidG {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 8px solid #101923;
  animation: 2s ease 0s infinite normal none running kBruCf;
}
.gNkidG span {
  vertical-align: top;
  display: inline-block;
  width: 42px;
  background-color: #101923;
  animation: 2s ease-in 0s infinite normal none running dMiuQA;
}
.preloader-text {
  margin-top: 34px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
}
@keyframes kBruCf {
  0% {
      transform: rotate(0deg);
  }
  25% {
      transform: rotate(180deg);
  }
  50% {
      transform: rotate(180deg);
  }
  75% {
      transform: rotate(360deg);
  }
  100% {
      transform: rotate(360deg);
  }
}
@keyframes dMiuQA {
  0% {
      height: 0%;
  }
  25% {
      height: 0%;
  }
  50% {
      height: 100%;
  }
  75% {
      height: 75%;
  }
  100% {
      height: 0%;
  }
}
